import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import { fetchImages } from './pixabay-api'; // –£–±–µ–¥–∏—Ç–µ—Å—å –≤ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏ –ø—É—Ç–∏

const gallery = document.querySelector('.gallery');
let lightbox;

// –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è Lightbox
const initLightbox = () => {
  if (lightbox) lightbox.destroy();
  lightbox = new SimpleLightbox('.gallery a', {
    captionsData: 'alt',
    captionDelay: 250,
  });
};

// –û—Å–Ω–æ–≤–Ω–∞—è –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞
export const renderImages = async (searchQuery) => {
  try {
    gallery.innerHTML = '';
    
    if (!searchQuery || typeof searchQuery !== 'string') {
      throw new Error('Invalid search query');
    }

    const data = await fetchImages(searchQuery);
    
    if (!data || !data.hits || data.hits.length === 0) {
      showErrorMessage();
      return;
    }

    await createGalleryMarkup(data.hits);
    initLightbox();
    
  } catch (error) {
    console.error('Render error:', error);
    showErrorMessage();
  }
};

// –°–æ–∑–¥–∞–Ω–∏–µ —Ä–∞–∑–º–µ—Ç–∫–∏ –≥–∞–ª–µ—Ä–µ–∏
const createGalleryMarkup = async (images) => {
  return new Promise((resolve) => {
    const markup = images.map(({ webformatURL, largeImageURL, tags, likes, views, comments, downloads }) => `
      <div class="gallery-item">
        <a href="${largeImageURL}">
          <img src="${webformatURL}" alt="${tags}" loading="lazy" />
        </a>
        <div class="image-info">
          <div class="item">
            <span class="label">Likes</span>
            <span class="count">${likes}</span>
          </div>
          <div class="item">
            <span class="label">Views</span>
            <span class="count">${views}</span>
          </div>
          <div class="item">
            <span class="label">Comments</span>
            <span class="count">${comments}</span>
          </div>
          <div class="item">
            <span class="label">Downloads</span>
            <span class="count">${downloads}</span>
          </div>
        </div>
      </div>
    `).join('');

    gallery.insertAdjacentHTML('beforeend', markup);
    resolve();
  });
};

// –ü–æ–∫–∞–∑ –æ—à–∏–±–∫–∏
export const showErrorMessage = () => {
  gallery.innerHTML = `
    <p class="error-message">
      Sorry, no images match your search. Please try again!
    </p>
  `;
};

loadInitialImages();







